import React, { Component } from 'react'
import './App.scss';
import { HashRouter, Route, Switch, Link } from 'react-router-dom';
import {withRouter} from "react-router-dom"
import Home from "./pages/Home/Home";
import Challenge from "./pages/Challenge/Challenge";
import Contact from "./pages/Contact/Contact";
import Dataset from "./pages/Dataset/Dataset";
import Participation from "./pages/Participation/Participation"
import logo from "./assets/logo.png";


class App extends Component {
	state = {
		LinkClassName : "/"
	}
	componentDidMount(){
		this.setState({
			LinkClassName:this.props.location.pathname
		}) 
	}
	
	render() {
		const {LinkClassName} = this.state
		return (
			<div className="App">
				<HashRouter>
					<div className="page-header">
						<div className="center-header">
							<div className="logo">
								<img src={logo} alt="logo" />
							</div>
							<ul className="header-nav">
								<li className={LinkClassName === "/" ?"active":""}><Link onClick={()=>{this.setState({LinkClassName:"/"})}} to={"/"}>Home</Link></li>
								<li className={LinkClassName === "/challenge" ?"active":""}><Link onClick={()=>{this.setState({LinkClassName:"/challenge"})}}  to={"/challenge"}>Challenge</Link></li>
								<li className={LinkClassName === "/participation" ?"active":""}><Link onClick={()=>{this.setState({LinkClassName:"/participation"})}}  to={"/participation"}>Participation</Link></li>
								<li className={LinkClassName === "/dataset" ?"active":""}><Link onClick={()=>{this.setState({LinkClassName:"/dataset"})}}  to={"/dataset"}>Dataset</Link></li>
								<li className={LinkClassName === "/contact" ?"active":""}><Link onClick={()=>{this.setState({LinkClassName:"/contact"})}}  to={"/contact"}>Contact</Link></li>
							</ul>
						</div>
					</div>
					<Switch>
						<Route exact path="/participation" component={Participation} />
						<Route exact path="/contact" component={Contact} />
						<Route exact path="/dataset" component={Dataset} />
						<Route exact path="/challenge" component={Challenge} />
						<Route exact path="/" component={Home} />
					</Switch>
				</HashRouter>
			</div>
		);
	}
}

export default withRouter( App );
